import { Radio, Statistic, Row, Col, Divider, Input, Table } from 'antd';
import React from 'react';
import Breadcrumbs from '../Common/Breadcrumbs';

const Manage = () => {
    const path = [
        'Products',
        'Manage'
    ]

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: text => <a>{text}</a>,
            sorter: true,
        },
        {
            title: 'SKU',
            dataIndex: 'sku',
            key: 'sku',
            sorter: true,
        },
        {
            title: 'Created At',
            dataIndex: 'created',
            key: 'created',
            sorter: true,
        }, {
            title: 'Retail Price',
            dataIndex: 'retail',
            key: 'retail',
            sorter: true,
        }, {
            title: 'Sale Price',
            dataIndex: 'sale',
            key: 'sale',
            sorter: true,
        }, {
            title: 'Lowest Price',
            dataIndex: 'lowest',
            key: 'lowest',
            sorter: true,
        }, {
            title: 'Available',
            dataIndex: 'available',
            key: 'available',
        }, {
            title: 'Visibles',
            dataIndex: 'visibles',
            key: 'visibles',
        }, {
            title: 'Action',
            dataIndex: 'action',
            key: 'action',
        }
    ];

    const tabledata = [
        {
            key: '1',
            name: 'John Brown',
            sku: 32,
            created: '2020-11-2',
            retail: '12312',
            sale: '12323',
            lowest: '231',
            available: 4,
            visibles: 'asdas, asd',
        }, {
            key: '1',
            name: 'John Brown',
            sku: 32,
            created: '2020-11-2',
            retail: '12312',
            sale: '12323',
            lowest: '231',
            available: 4,
            visibles: 'asdas, asd',
        }, {
            key: '1',
            name: 'John Brown',
            sku: 32,
            created: '2020-11-2',
            retail: '12312',
            sale: '12323',
            lowest: '231',
            available: 4,
            visibles: 'asdas, asd',
        }
    ];
    return (
        <div>
            <Breadcrumbs crumb={path} />
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Row gutter={16} className='product-stat'>
                    <Col span={5}>
                        <Statistic title="All" value={1128} />
                    </Col>
                    <Col span={4}>
                        <Statistic title="Live" value={93} />
                    </Col>
                    <Col span={5}>
                        <Statistic title="Image Missing" value={93} />
                    </Col>
                    <Col span={5}>
                        <Statistic title="Poor Quality" value={93} />
                    </Col>
                    <Col span={5}>
                        <Statistic title="Sold Out" value={93} />
                    </Col>
                </Row>
                <Divider />
                <Radio.Group size='large'>
                    <Radio.Button value="Add New">Add New</Radio.Button>
                    <Radio.Button value="Export">Export</Radio.Button>
                    <Radio.Button value="Shop Category">Shop Category</Radio.Button>
                    <Radio.Button value="Import">Import</Radio.Button>
                    <Radio.Button value="View History">View History</Radio.Button>
                    <Radio.Button value="Name">Name</Radio.Button>
                    <Radio.Button value="Seller SKU">Seller SKU</Radio.Button>
                    <Radio.Button value="Shop SKU">Shop SKU</Radio.Button>
                    <Radio.Button value="Brand Name">Brand Name</Radio.Button>
                    <Radio.Button value="Brand Name">Add Product SKU Code</Radio.Button>
                </Radio.Group>
                <Divider />
                <Table columns={columns} dataSource={tabledata} />
            </div>
        </div >
    );
};

export default Manage;