import React from 'react';
import { Form, Input, Button, Row, Col, Select, InputNumber, Upload, Tooltip } from 'antd';
import { UploadOutlined, PlusOutlined } from '@ant-design/icons';

import Breadcrumbs from '../Common/Breadcrumbs';
import { layout, rowlayout, tailLayout } from '../../constants/layout'

const New = () => {
    const path = [
        'Products',
        'New'
    ]

    const onFinish = (values) => {
        console.log('Success:', values);
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };
    return (
        <div>
            <Breadcrumbs crumb={path} />
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Form
                    {...layout}
                    name="basic"
                    initialValues={{
                        remember: false,
                    }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Row>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Item Code"
                                name="itemCode"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input item code!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Product Name"
                                name="productName"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input a product name!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Product Category"
                                name="productCategory"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please select a product category!',
                                    },
                                ]}
                            >
                                <Select />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Product Subcategory"
                                name="productSubCategory"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please select a product sub category!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Gender"
                                name="gender"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please selct a gender!',
                                    },
                                ]}
                            >
                                <Select />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Sale Price"
                                name="salePrice"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input a sale price!',
                                    },
                                ]}
                            >
                                <InputNumber />
                            </Form.Item>
                        </Col><Col {...rowlayout}>
                            <Form.Item
                                label="Retail Price"
                                name="retailPrice"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input a retail price!',
                                    },
                                ]}
                            >
                                <InputNumber />
                            </Form.Item>
                        </Col><Col {...rowlayout}>
                            <Form.Item
                                label="Main Image"
                                name="mainImage"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please upload a main image!',
                                    },
                                ]}
                            >
                                <Upload >
                                    <Tooltip title="Image aspect ratio must be 1.25 and size less than 800kb." placement="right">
                                        <Button icon={<UploadOutlined />}>Click to Upload</Button>
                                    </Tooltip>                                </Upload>
                            </Form.Item>
                        </Col><Col {...rowlayout}>
                            <Form.Item
                                label="Auxillary Image 1"
                                name="auxImage1"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please upload an auxillary image!',
                                    },
                                ]}
                            >
                                <Upload >
                                    <Tooltip title="Image aspect ratio must be 1.25 and size less than 800kb." placement="right">
                                        <Button icon={<UploadOutlined />}>Click to Upload</Button>
                                    </Tooltip>                                </Upload>
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Auxillary Image 2"
                                name="auxImage2"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input an auxillary image!',
                                    },
                                ]}
                            >
                                <Upload >
                                    <Tooltip title="Image aspect ratio must be 1.25 and size less than 800kb." placement="right">
                                        <Button icon={<UploadOutlined />}>Click to Upload</Button>
                                    </Tooltip>
                                </Upload>
                            </Form.Item>
                        </Col><Col {...rowlayout}>
                            <Form.Item
                                label="Tags"
                                name="tags"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input tags!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                        </Col>
                        <Col {...rowlayout}>
                            <Form.Item
                                label="Add Color and Size Details"
                                name="extras"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please add color and size details!',
                                    },
                                ]}
                            >
                                <Upload >
                                    <Button icon={<PlusOutlined />}>Click to Add</Button>
                                </Upload>
                            </Form.Item>
                        </Col>

                    </Row>
                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            Submit
                            </Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    );
};

export default New;